////
/// Schemes Module : Vars
/// @group schemes
////

/// Dfinition des custom properties de couleur
/// @access public
@mixin define-vars {
  /* valeurs des couleurs en dark mode */
  #{$dark-theme-selector} {
    @each $name, $set in $scheme-sets {
      --#{$name}: #{_scheme-set-color($set, dark)};
    }
  }

  /* valeur des couleurs par dfaut */
  :root {
    @each $name, $set in $scheme-sets {
      --#{$name}: #{_scheme-set-color($set, light)};
    }
  }

  /* valeur des couleurs dans des blocs de combinaison */
  #{$not-dark-theme-selector} {
    $i: 0;
    @each $type, $variation in $scheme-blocks {
      $i: ($i + 1);

      /* le slecteur est un bloc de combinaison de couleur du type dfini */
      [class*="#{$base-scheme}-#{$type}"],
      /* le selecteur contient un modifier de bloc de couleur du type dfini */
      [class*="--scheme-#{$type}-"] {
        @each $name, $set in $scheme-sets {
          @if length($set) >= $i {
            --#{$name}: #{_scheme-set-color($set, $type)};
          }
        }
      }
    }
  }
}
