////
/// Schemes Settings
/// @group schemes
////

// TODO: import  enlever aprs intgration des schemes dans le core
@import "~@gouvfr/core/src/styles/global/functions/selectors";

// Attribut utilis pour le dark mode
$dt: '#{ns-attr(theme, "dark")}';

// Slecteur du dark mode
$dark-theme-selector: ':root#{$dt}';

// Slecteur du light mode
$not-dark-theme-selector: ':root:not(#{$dt})';

// Base de construction de classe de block de combinaison
$base-scheme: #{ns(scheme, '')};

// Base de construction de slecteur de block de combinaison
$base-scheme-selector: #{ns(scheme)};

// Slecteur de transition de couleur
$theme-transition-selector: ':root#{ns-attr(transition)}';

// Dure de transition de couleur
$theme-transition-duration: 0.3s;

// Les transitions sont incluses dans le code
$theme-allow-transition: false;

// Variable de couleur utilise dans les mixin d'lments de combinaison de couleur
$COLOR:unquote('$COLOR') !global;

@import 'settings/colors';
@import 'settings/sets';
@import 'settings/blocks';
