/// Fonction de transformation d'une string en nombre intgral
/// @access public
/// @param {String} $value - le nombre en chane de caractres
/// @param {Number} $max - maximum d'itration de la boucle  partir de laquelle l'action est abandandonne
@function str-to-num($value, $max:1000) {
  @for $i from 0 to $max {
    @if $value == quote($i) {
      @return $i;
    }
    @else if $value == quote(-$i) {
      @return -$i;
    }
  }
  @return null;
}

/// Remplacement dans une chaine de caractres
/// @access public
/// @param {String} $stack - chaine de caractre o chercher
/// @param {String} $needle - lment  chercher
/// @param {String} $content - lment de remplacement
/// @return {String} la chane de caractres transforme
@function str-replace($stack, $needle, $content) {
  $is:quote($stack);
  $in:quote($needle);
  $ic:quote($content);

  $i:str-index($is, $in);

  @while $i != null {
    $is:unquote(str_slice($is, 1, $i - 1) + $ic + str_slice($is, $i + str-length($in), str_length($is)));
    $i:str-index($is, $in);
  }

  @return unquote($is);
}

/// Dcoupage d'une chaine de caractres avec un sparateur particulier
/// @access public
/// @param {String} $string - chaine de caractre  dcouper
/// @param {String} $separator - sparateur
@function str-split($string, $separator:',') {
  $tmp: str-slice($string, 1);
  $list: ();
  $index: str-index($string, $separator);

  @if $index == null {
    $list: ($string);
  }
  @else {
    $list: join((str-slice($string, 1, $index - 1)), str-split(str-slice($string, $index + 1)));
  }
  @return $list;
}
