////
/// Grid Tools : colonne
/// @group grid
////

/// Dfinit la largeur de colonne
///
/// @param {Number} $size - Proportion de la taille de colonne
/// @param {Number} $columns [$grid-columns] - Nombre de colonne dans la grille (Par dfaut `$grid-columns` = 12)
///
/// @require {variable} $grid-columns
///
/// @example scss - Dfinit la taille de colonne  6 sur 12
///   .foo {
///     @include grid-col(6);
///   }
@mixin grid-col($size, $columns: 12) {
  $percent: #{100% * $size};
  $calc: 'calc(#{$percent} / #{$columns})';
  flex: 0 0 #{$calc};
  width: #{$calc};
  max-width: #{$calc};
}

/// Dfinit le dcalage de colonne (cr une marge  gauche)
///
/// @param {Number} $size - Proportion du dcalage de colonne
/// @param {Number} $columns [$grid-columns] - Nombre de colonne dans la grille (Par dfaut `$grid-columns` = 12)
///
/// @require {variable} $grid-columns
///
/// @example scss - Dfinit la taille du dcalage  2 colonnes sur 12
///   .foo {
///     @include grid-col-offset(2);
///   }
@mixin grid-col-offset($size, $columns: 12) {
  $percent: #{100% * $size};
  $calc: 'calc(#{$percent} / #{$columns})';

  @if $size == 0 {
    $calc: '0';
  }
  @include margin-left($calc);
}
